import { mapState } from 'vuex'
export default {
    data() {
        return {
            showBox:false,
            aboutInfo: ''
        }
    },
    created() {
        this.hasLogin()
    },
    computed: {
        ...mapState(['userInfo'])
    },
    methods: {
        gotoLoginOut () {                
            uni.navigateTo({
                url: '../login/login?pageType=loginOut',
            });
        },
        bindLogin() {
            uni.reLaunch({
                url: '../login/login',
            });
        },
        hasLogin () {
            let hasLogin = !!this.userInfo
            if (!hasLogin) {
                this.bindLogin()
            }
        },
        intoOrder () {
            uni.navigateTo({
                url: '../order/order',
            });
        },
        intoChangePassword () {
            uni.navigateTo({                
                url: '../signUp/signUp?page=changePassword'
            })
        },
        async getAbout () {
            if (!this.userInfo) return
            let res = await this.$api.getAboutList({type:'about'})
            if (res.code === '0') {
                let ele = await this.$api.getAbout(res.data[0].id)
                if (ele.code === '0') {
                    this.aboutInfo = ele.data.content
                    this.version = ele.data.version
                    let temp = uni.getStorageSync('about-version')
                    if (temp !== this.version) {
                        uni.setStorageSync('about-version',this.version)                            
                        this.showBox = true
                    }
                }
            }
            
        },
        tapAbout () {
            this.getAbout()
            this.showBox = true
        },
        closeBox () {
            this.showBox = false
        }
    }
}
